.promo {
    min-height: 650px;
    padding: 21px 0 93px 0;
    &__wrapper {
        margin-top: 91px;
        display: flex;
        flex-direction: column;
        align-items: flex-end;
    }
    &__header {
        color: #ffffff;
        font-size: 42px;
        font-weight: 500;
        text-transform: uppercase;
    }
    &__subheader {
        color: #ffffff;
        font-size: 24px;
        font-weight: 300;
        text-transform: uppercase;
    }
    &__icons {
        width: 425px;
        margin-top: 70px;
        display: flex;
        justify-content: space-between;
    }
    &__icon {
        width: 108px;
        display: flex;
        flex-direction: column;
        align-items: center;
        color: #ffffff;
        font-size: 14px;
        font-weight: 500;
        text-align: center;
        img {
            display: block;
            margin: 0 auto;
            margin-bottom: 20px;
        }
    }
    &__link {
        margin-top: 18px;
        color: #ffffff;
        font-size: 16px;
        font-weight: 500;
        a { 
            color: #ffffff;
            text-transform: uppercase;
            text-decoration: underline;
        }
    }
}
